@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@100;200;300;400;500;600;700&family=Limelight&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:ital,wght@1,300&display=swap);
:root {
  --black: rgb(48, 44, 41);
  --orange: #df7861;
  --bg: #fffdf4;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Josefin Sans", sans-serif;
  background-color: #fffdf4;
  background-color: var(--bg);
  line-height: 1.3;
  color: rgb(48, 44, 41);
  color: var(--black);
}

html {
  scroll-behavior: smooth;
}

h1 {
  font-family: "Limelight", cursive;
  font-size: 64px;
  font-weight: normal;
}

h2 {
  font-size: 42px;
}

h1,
h2,
h3,
p {
  margin: 0;
}

.header {
  text-align: center;
}

ul {
  margin: 0;
  margin-right: 70px;
  margin-left: 27px;
  padding: 0;
  line-height: 1;
}

li {
  font-size: 30px;
}
li span {
  font-size: 20px;
}

.text-orange {
  color: #df7861;
  color: var(--orange);
}

.text-sm {
  font-size: 22px;
}

.text-md {
  font-size: 28px;
}

.text-lg {
  font-size: 35px;
}

.text-300 {
  font-weight: 300;
}

.text-700 {
  font-weight: 700;
}

.text-20px {
  font-size: 20px;
}

.list-bullet {
  vertical-align: middle;
}

a:hover {
  text-decoration: none;
}

#logo-personal {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

#logo-personal p {
  font-style: italic;
  font-weight: 300;
  text-align: center;
  width: 80%;
}

.tab-link:focus-visible {
  outline: 4px solid rgb(48, 44, 41);
  outline: 4px solid var(--black);
}

.app {
  width: 90%;
  margin: 0 auto;
}

#intro-container {
  max-width: 800px;
  margin: 0 auto;
  margin-top: 150px;
  margin-bottom: 200px;
}

#intro {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: flex-end;
          align-items: flex-end;
  position: relative;
  margin-bottom: 40px;
}

/* .tech-stack {
  display: flex;
} */

.contact-button-screen {
  display: none !important;
}

.intro-left {
  width: 60%;
  height: 100%;
}

#portrait {
  width: 35%;
  object-fit: cover;
  object-position: center;
  border-radius: 8px;
}

@media screen and (max-width: 500px) {
  #intro {
    margin-top: 70px;
  }
  /* .tech-stack {
    display: initial;
  } */
}

@media screen and (max-width: 750px) {
  #intro-container {
    margin-bottom: 126px;
  }
  #intro {
    display: block;
  }
  .intro-left,
  #portrait {
    width: 100%;
    margin-top: 42px;
  }

  .contact-button {
    display: none !important;
  }

  .contact-button-screen {
    display: inline-block !important;
  }
}

.button-pill {
  margin-top: 36px;
  border: 2px solid var(--black);
  border-radius: 40px;
  padding: 8px 28px 5px 28px;
  background: #fffef9;
  box-shadow: 1px 1px 0 0 var(--black), 2px 2px 0 0 var(--black),
    3px 3px 0 0 var(--black);
  font-weight: 500;
  color: var(--black);
  display: inline-block;
}

.button-pill:hover {
  cursor: pointer;
  box-shadow: 1px 1px 0 0 var(--black), 2px 2px 0 0 var(--black);
  -webkit-transform: translate(1px, 1px);
          transform: translate(1px, 1px);
  text-decoration: none;
  color: var(--black);
}

.button-pill:active {
  box-shadow: none;
  -webkit-transform: translate(3px, 3px);
          transform: translate(3px, 3px);
}

.button-pill:focus-visible {
  outline: 4px solid var(--orange);
}

#projects {
  margin: 0 auto;
  margin-top: -130px;
  padding-top: 130px;
  margin-bottom: 200px;
  width: 60%;
}

.scroll-anchor {
  position: relative;
  top: -130px;
  left: 0;
}

@media screen and (max-width: 1500px) {
  #projects {
    width: 80%;
  }
}

@media screen and (max-width: 1100px) {
  #projects {
    width: 100%;
  }
}

@media screen and (max-width: 974px) {
  #projects {
    margin-top: -300px;
    padding-top: 300px;
  }
}

.project-container {
  width: 85%;
  margin: 50px auto 130px auto;
}

.project-item {
  width: 100%;
  border-radius: 34px 34px 150px 34px;
}

.project-text {
  margin-top: 20px;
}

.project-info {
  width: 100%;
  padding-right: 0;
}

/* IMAGE */

.image {
  width: 100%;
  height: 0;
  padding-top: 63%;
  background-position: center;
  background-size: cover;
  border-radius: 8px;
}

.image-container {
  width: 100%;
  position: relative;
}

/* LINKS  */

.link {
  transition: 0.2s ease-in;
}

.link:hover {
  color: var(--black);
}

/* TAGS */

.tech-tags {
  width: 80%;
  margin-top: 16px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.tech-tag {
  color: white;
  background-color: var(--orange);
  padding: 7px 18px 4px 18px;
  border-radius: 20px;
  font-weight: 400;
  margin: 10px 10px 0 0;
}

/* RESPONSIVENESS */

@media screen and (max-width: 450px) {
  .project-item {
    padding: 22px;
  }
  .links {
    top: 17px;
    right: 16px;
  }
}

@media screen and (max-width: 800px) {
  .project-container {
    width: 100%;
  }
}

.illustration-container {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: 50px auto 50px auto;
}

.illustration {
  width: 100%;
  border-radius: 8px;
}

.filter-container {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  position: relative;
}

.filter-button {
  color: white;
  background: var(--black);
  font-size: 20px;
  font-weight: 500;
  cursor: pointer;
  border: 2px solid var(--black);
  border-radius: 40px;
  padding: 10px 20px 8px 20px;
  z-index: 200;
  width: 308px;
}

.filter-dropdown {
  width: 300px;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  background-color: var(--bg);
  border: 3px solid var(--black);
  border-radius: 25px;
  position: absolute;
  z-index: 100;
  width: 308px;
  top: 0px;
  left: 50% !important;
  right: auto !important;
  text-align: center !important;
  -webkit-transform: translate(-50%, 0) !important;
          transform: translate(-50%, 0) !important;
}

.filter-dropdown ul {
  list-style: none;
  text-align: center;
  margin: 64px auto 30px auto;
}

.filter-option {
  font-size: 20px;
  font-weight: 700;
  margin-bottom: 20px;
}

#filter-text:hover {
  cursor: pointer;
  border-bottom: 3px solid var(--black);
}

.filter-container-sticky {
  position: -webkit-sticky;
  position: sticky;
  z-index: 200;
  top: 130px;
}

.tabs {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  list-style: none;
  margin: 0;
}

.tab {
  color: var(--black);
  font-size: 24px !important;
  font-weight: 700;
  margin-right: 36px;
  cursor: pointer;
}

.tabs li:last-child p {
  margin: 0;
}

.arrow {
  font-size: 16px;
}

.tab:hover {
  border-bottom: 3px solid var(--black);
}

.underline-tab {
  border-bottom: 3px solid var(--black);
}

.underline-filter {
  border-bottom: 3px solid var(--black);
}

#contact {
  max-width: 800px;
  margin: -80px auto 200px auto;
  padding-top: 80px;
}

#contact p {
  overflow-wrap: break-word;
}

.contact-link {
  transition: 0.2s ease-in;
  color: var(--orange);
  font-weight: 400;
  text-decoration: none;
}

#contact a:hover {
  color: var(--black);
}

@media screen and (max-width: 974px) {
  #contact {
    margin-top: -300px;
    padding-top: 300px;
  }
}

.footer {
  text-align: center;
}

.social-links {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.social-links p {
  font-size: 46px;
  margin: 0 10px;
  transition: 0.2s ease-in;
}

.social-links p:hover {
  color: var(--black);
}

.navbar-logo {
  cursor: pointer;
  width: 80px;
}

.navbar-logo:hover {
  opacity: 0.8;
}

header {
  border-bottom: 2px solid var(--black);
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  height: 100px;
  padding: 10px 3%;
  background-color: var(--bg);
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 500;
}

header nav {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

header .nav-btn {
  padding: 5px;
  cursor: pointer;
  background: transparent;
  border: none;
  outline: none;
  display: none;
  font-size: 1.8rem;
}

nav a {
  margin: 0 1.5rem;
  color: var(--black);
  text-decoration: none;
  font-weight: 700;
  font-size: 18px;
}

nav a:hover {
  color: var(--orange);
}

/* CHECKBOX */

/* The switch - the box around the slider */
.lang-switch-container {
  margin-left: 1.5rem;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.lang-switch {
  position: relative;
  display: inline-block;
  width: 42px;
  height: 24px;
}

/* Hide default HTML checkbox */
.lang-switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.lang-switch-container p {
  font-size: 16px;
}

/* The slider */
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: var(--orange);
  transition: 0.4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 16px;
  width: 16px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  transition: 0.4s;
}

input:checked + .slider {
  background-color: #8e75f3;
}

input:checked + .slider:before {
  -webkit-transform: translateX(18px);
  transform: translateX(18px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

@media screen and (max-width: 1000px) {


  header .nav-btn {
    display: block;
  }

  header nav {
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    text-align: center;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    grid-gap: 1.5rem;
    gap: 1.5rem;
    transition: 0.5s;
    -webkit-transition: 0.5s;
    transform: translateY(-100vh);
    -webkit-transform: translateY(-100vh);
    -ms-transform: translateY(-100vh);
    background-color: var(--bg);
    z-index: 1000;
  }

  nav a {
    font-size: 26px;
  }

  header .responsive-nav {
    -webkit-transform: none;
            transform: none;
  }

  nav .nav-close-btn {
    position: absolute;
    top: 24px;
    right: 3%;
  }

  .lang-switch-container {
    margin-right: 1.5rem;
  }
}
