.filter-container {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  position: relative;
}

.filter-button {
  color: white;
  background: var(--black);
  font-size: 20px;
  font-weight: 500;
  cursor: pointer;
  border: 2px solid var(--black);
  border-radius: 40px;
  padding: 10px 20px 8px 20px;
  z-index: 200;
  width: 308px;
}

.filter-dropdown {
  width: 300px;
  height: fit-content;
  background-color: var(--bg);
  border: 3px solid var(--black);
  border-radius: 25px;
  position: absolute;
  z-index: 100;
  width: 308px;
  top: 0px;
  left: 50% !important;
  right: auto !important;
  text-align: center !important;
  transform: translate(-50%, 0) !important;
}

.filter-dropdown ul {
  list-style: none;
  text-align: center;
  margin: 64px auto 30px auto;
}

.filter-option {
  font-size: 20px;
  font-weight: 700;
  margin-bottom: 20px;
}

#filter-text:hover {
  cursor: pointer;
  border-bottom: 3px solid var(--black);
}

.filter-container-sticky {
  position: sticky;
  z-index: 200;
  top: 130px;
}

.tabs {
  display: flex;
  justify-content: center;
  list-style: none;
  margin: 0;
}

.tab {
  color: var(--black);
  font-size: 24px !important;
  font-weight: 700;
  margin-right: 36px;
  cursor: pointer;
}

.tabs li:last-child p {
  margin: 0;
}

.arrow {
  font-size: 16px;
}

.tab:hover {
  border-bottom: 3px solid var(--black);
}

.underline-tab {
  border-bottom: 3px solid var(--black);
}

.underline-filter {
  border-bottom: 3px solid var(--black);
}
